import { Injectable } from '@angular/core';
import { Observable, of, Observer, BehaviorSubject } from 'rxjs';
import { DataHtml } from './model/data-html.model';
@Injectable({
  providedIn: 'root'
})
export class DataService {
  private titulo = new BehaviorSubject('default message');

  constructor() {

   }

   getTitle() {
     return this.titulo.asObservable();
   }

  setTitle(title: string) {
    this.titulo.next(title);
  }
}
